/* Import Bootstrap */
@import url('https://cdn.jsdelivr.net/npm/bootstrap@5.3.0/dist/css/bootstrap.min.css');

/* Background */
.search-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: linear-gradient(135deg, #404050, #34373a);
  overflow: hidden;
}
.search-container, .sidebar {
  display: flex
;
  flex-direction: column;
  margin: 0px !important;
  padding: 17px !important;
}
.active>.page-link, .page-link.active {
  z-index: 3;
  color: var(--bs-pagination-active-color);
  background-color: #dc3545;
  border-color: none;
}

.sidebar {
  align-items: center;
  background: #000;
  border-radius: 10px;
  box-shadow: 3px 0 15px #0003;
  color: #fff;
  max-width: 300px;
  padding: 20px;
  width: 100%;
  height: fit-content;
}

@media (max-width: 768px) {
  .sidebar {
    width: 100%;
    max-width: none;
    border-right: none;
  }
}
 .data1 {

  padding: 0px !important;
}
/* Title */
.sch_title {
  font-size: 22px;
  font-weight: bold;
  color: #EEEEFF;
  margin-bottom: 20px;
  text-align: center;
}

/* Filters */
.sch_filter {
  width: 100%;
  background: #7F7CAF;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
}

.sch_input {
  width: 100%;
  padding: 12px;
  border-radius: 8px;
  border: none;
  font-size: 14px;
}

.sch_input:focus {
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.5);
}

/* Buttons */
.sch_btn {
  background: #28587B;
  color: white;
  padding: 12px;
  font-size: 14px;
  border-radius: 8px;
  width: 100%;
  transition: transform 0.3s ease-in-out, background 0.3s ease-in-out;
}

.sch_btn:hover {
  background: #321C4E;
  transform: scale(1.05);
}

/* Dog Grid */
.sch_main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 20px;
}

.sch_dog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  width: 100%;
  max-width: 1200px;
  padding: 20px;
}

/* Pagination */
.sch_pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 20px;
}

.sch_page-btn {
  background: #28587B;
  color: white;
  padding: 10px 15px;
  font-size: 16px;
  border-radius: 8px;
  transition: 0.3s ease-in-out;
  border: none;
}

.sch_page-btn:hover {
  background: #321C4E;
  transform: scale(1.05);
}

.sch_page-info {
  font-size: 16px;
  font-weight: bold;
  color: #321C4E;
}

/* Responsive Design */
@media (max-width: 768px) {
  .search-container {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
    max-width: none;
    border-right: none;
  }

  .sch_dog-grid {
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  }

  .sch_pagination {
    flex-direction: column;
    gap: 10px;
  }

  .sch_btn, .sch_page-btn {
    width: 100%;
  }
}
/* Animation for filter container */
@keyframes slideIn {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

.sidebar {
  animation: slideIn 0.5s ease-in-out;
}

/* Pagination Button Styles */
.pagination .page-item .page-link {
  background-color: #28587B;
  color: white;
  border: none;
  margin: 0 5px;
  border-radius: 8px;
  transition: transform 0.3s ease-in-out, background 0.3s ease-in-out;
}

.pagination .page-item .page-link:hover {
  background-color: #321C4E;
  transform: scale(1.05);
}

.pagination .page-item.disabled .page-link {
  background-color: #ccc;
  color: #666;
}

.pagination .page-item.active .page-link {
  background-color: #dc3545;
  color: white;
}
/* Animation for filter */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}

.sch_filter {
  animation: fadeIn 0.3s ease-in-out;
}

/* Mobile Filter Button */
.mobile-filter-btn {
  display: none;
  background: #28587B;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  margin-bottom: 10px;
  width: 100%;
  text-align: center;
  cursor: pointer;
}

@media (max-width: 768px) {
  .mobile-filter-btn {
    display: block;
  }
  .sidebar {
    display: none;
  }
  .sidebar.open {
    display: flex;
    animation: fadeIn 0.3s ease-in-out;
  }
}

/* Pagination Buttons */
.sch_pagination-btn {
  background: #28587B;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}

.sch_pagination-btn:hover {
  background: #321C4E;
}

.sch_pagination-btn:disabled {
  background: #ccc;
  cursor: not-allowed;
}
input.form-control {
  padding: 10px !important;
  color: wheat;
}
input.form-control::after {
  padding: 10px !important;
  color: wheat;
}