/* Full-Screen Background with Darkened Corners */
.main-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background: 
    radial-gradient(circle, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.7) 100%),
    url('./dog.jpg') center/cover no-repeat;
  text-align: center;
  position: relative;
  max-width: 100% !important;
  margin: 0%!important;
  padding: 0%!important;
}

/* Small Header: Fetch Dog Adoption */
.small-header {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1.2rem;
  font-weight: bold;
  color: white;
  letter-spacing: 1px;
  text-transform: uppercase;
}

/* Adopt Me Button (Bottom-Center) */
.adopt-btn {
  position: absolute;
  bottom: 40px;
  left: 50%;
  font-size: 1.5rem;
  padding: 1rem 2.5rem;
  border-radius: 5px;
  background: transparent;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  border: 2px solid white;
}

.adopt-btn1 {
  padding: 10px 18px;
  border-radius: 5px;
  background: transparent;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  border: 2px solid white;
}
.modal-header {
  color: #fff;
  text-transform: uppercase;
  font-size: 34px;
}
.modal-subtitle{
  color: white;
  text-align: center;
}
/* Hover Effect */
.adopt-btn:hover {
  background-color: #00000020;
  color: #FFF;
  border: 2px solid #00000020;
}
.adopt-btn:active {
  background-color: #000000;
  color: #FFF;
  border: 2px solid #000000;
}
.adopt-btn1:hover {
  background-color: #00000020;
  color: #FFF;
  border: 2px solid #00000020;
}
.adopt-btn1:active {
  background-color: #000000;
  color: #FFF;
  border: 2px solid #000000;
}
/* Login Modal: Glassmorphism */
.modal-content {
  border-radius: 15px;
  border: none;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  padding: 2rem;
}

.modal-title {
  font-size: 2rem;
  font-weight: bold;
  color: white;
  text-align: center;
  margin-bottom: 1rem;
}

/* Glass-Effect Input Boxes */
.form-control {
  margin-bottom: 1rem;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.4);
  background: rgba(255, 255, 255, 0.2);
  color: white;
  padding: 1.2rem;
}

/* Placeholder Styling */
.form-control::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

/* Input Focus */
.form-control:focus {
  border-color: white;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
  background: rgba(255, 255, 255, 0.3);
}

/* Take Me Home Button (Same as Adopt Button) */
.login-btn {
  width: 100%;
  padding: 1rem;
  font-size: 1.2rem;
  border-radius: 10px;
  background: transparent;
  color: white;
  border: 2px solid white;
  font-weight: bold;
}

/* Hover Effect */
.login-btn:hover {
  background-color: #00000020;
  color: #FFF;
  border: 2px solid #00000020;
}

/* Disable Effect */
.login-btn:disabled {
  background-color: rgba(255, 255, 255, 0.4);
  cursor: not-allowed;
}
.login-btn:click {
  background-color: #000000;
  color: #FFF;
  border: 2px solid #000000;
}